
[v-cloak] {
  display: none;
}
* {
  margin: 0;
  padding: 0;
}
#app {
  width: 1140px;
  margin-left: auto;
  margin-right: auto;
}
#app h1 {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 28px;
}
.div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.div .film {
  width: 25%;
  padding-right: 15px;
  text-align: center;
  margin-bottom: 15px;
}
.div .film img {
  width: 100%;
}
.loading {
  background: #333;
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 9999;
  color: #fff;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 25px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.film-detay {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 20px;
}
.film-detay input {
  width: 50%;
}
.film-detay input,
button {
  padding: 10px 20px;
  margin-bottom: 10px;
}



















































































